.Spinner {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}

.ldSpinner {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.ldSpinner div {
    display: inline-block;
    position: absolute;
    left: 8px;
    width: 16px;
    background: rgb(4, 175, 255);
    animation: ldSpinner 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}

.ldSpinner div:nth-child(1) {
    left: 8px;
    animation-delay: -0.24s;
}

.ldSpinner div:nth-child(2) {
    left: 32px;
    animation-delay: -0.12s;
}

.ldSpinner div:nth-child(3) {
    left: 56px;
    animation-delay: 0;
}

@keyframes ldSpinner {
    0% {
        top: 8px;
        height: 64px;
    }

    50%,
    100% {
        top: 24px;
        height: 32px;
    }
}